export class Map {
    constructor(id) {
        this._drag = {
            flag: false,
            start: {
                x: 0,
                y: 0
            },
            end: {
                x: 0,
                y: 0
            }
        };
        this._geometries = [];
        this._canvas = document.getElementById(id);
        this._ctx = this._canvas.getContext("2d");
        this._onDoubleClick = this._onDoubleClick.bind(this);
        this._onMouseDown = this._onMouseDown.bind(this);
        this._onMouseMove = this._onMouseMove.bind(this);
        this._onMouseUp = this._onMouseUp.bind(this);
        this._onWheel = this._onWheel.bind(this);
        this._canvas.addEventListener("dblclick", this._onDoubleClick);
        this._canvas.addEventListener("mousedown", this._onMouseDown);
        this._canvas.addEventListener("mousemove", this._onMouseMove);
        this._canvas.addEventListener("mouseup", this._onMouseUp);
        this._canvas.addEventListener("wheel", this._onWheel);
    }
    addGeometry(geometry) {
        geometry.draw(this._ctx);
        this._geometries.push(geometry);
    }
    redraw() {
        this._ctx.save();
        //ËÆæÁΩÆÂõûÂéüÂßãÁü©Èòµ
        this._ctx.setTransform(1, 0, 0, 1, 0, 0);
        this._ctx.clearRect(0, 0, this._canvas.width, this._canvas.height);
        this._ctx.restore();
        this._geometries.forEach(geometry => geometry.draw(this._ctx));
    }
    clear() {
        this._ctx.setTransform(1, 0, 0, 1, 0, 0);
        this._ctx.clearRect(0, 0, this._canvas.width, this._canvas.height);
    }
    _onDoubleClick(event) {
        this._ctx.scale(2, 2);
        this.redraw();
    }
    _onMouseDown(event) {
        this._drag.flag = true;
        this._drag.start.x = event.x;
        this._drag.start.y = event.y;
    }
    _onMouseMove(event) {
    }
    _onMouseUp(event) {
        if (this._drag.flag) {
            this._drag.end.x = event.x;
            this._drag.end.y = event.y;
            const matrix = this._ctx.getTransform();
            //Áü©ÈòµËΩ¨Êç¢ Âπ≥Áßª‰∏Ä‰∏ã
            this._ctx.translate((this._drag.end.x - this._drag.start.x) / matrix.a, (this._drag.end.y - this._drag.start.y) / matrix.d);
            this.redraw();
        }
        this._drag.flag = false;
    }
    _onWheel(event) {
        event.preventDefault();
        let scale = 1;
        const sensitivity = 100;
        const delta = event.deltaY / sensitivity;
        if (delta < 0) {
            // ÊîæÂ§ß
            scale *= delta * -2;
        }
        else {
            // Áº©Â∞è
            scale /= delta * 2;
        }
        //‰∫§‰∫íË°®Áé∞‰∏∫ Èº†Ê†áÂΩìÂâç‰ΩçÁΩÆ Â±èÂπïÂùêÊ†á‰∏çÂèò ËøõË°åÁº©Êîæ Âç≥x2 = x1
        //Á¨¨‰∏ÄÁßçÊñπÊ°àÔºåÂùêÊ†áÁ≥ª‰∏çÂèòÔºåÂèòÂùêÊ†áÂÄº
        //1.Â∞ÜÂéüÂ±èÂπïÂùêÊ†á x1 ËΩ¨Êàê ÂàùÂßãÂùêÊ†á x0 = (x1 - e1) / a1  ÂàùÂßãÁü©Èòµ (1,0,0,1,0,0)
        //2.ÂàùÂßãÂùêÊ†áx0 ËΩ¨Êàê Áé∞Â±èÂπïÂùêÊ†áx2  a2 * x0 + e2 = x2    e2 = x2 - a2 * x0  ‰ª£ÂÖ•1Âºè e2 = x2 - a2 * (x1 - e1) / a1
        //3.Â∑≤Áü•scale = a2 / a1  ÊïÖ e2 = x2 - scale * (x1 - e1)
        //4.Âè¶Áü©ÈòµÂèòÊç¢ a1 * e + e1 = e2
        //5.ËÅîÁ´ã3Âíå4  Ê±ÇÂæó e = (x2 - scale * (x1 - e1) - e1) / a1
        //PS: Á¨¨4ÁÇπËØ¶Ëß£: matrix1 * matrix = matrix2Ôºõmatrix1ÂΩìÂâçÁü©ÈòµÔºåmatrix2‰∏∫ÂèòÊç¢ÂêéÁöÑÁªìÊûúÁü©ÈòµÔºåmatrix‰∏∫ÂèòÊç¢Áü©ÈòµÔºõÊàë‰ª¨Áé∞Âú®Ë¶ÅÊ±ÇmatrixÁöÑeÔºåËÄåÊ†πÊçÆÁü©ÈòµËÆ°ÁÆóÔºåa1 * e + e1 = e2„ÄÇÂÖ∂‰∏≠a1ÊòØmatrix1ÁöÑaÔºåeÊòØmatrixÁöÑeÔºåe1ÊòØmatrix1ÁöÑeÔºåe2ÊòØmatrix2ÁöÑe„ÄÇ
        const matrix = this._ctx.getTransform();
        const a1 = matrix.a, e1 = matrix.e, x1 = event.x, x2 = x1; //ÊîæÂ§ßÂà∞‰∏≠ÂøÉÁÇπ x2 = this._canvas.width / 2
        const e = (x2 - scale * (x1 - e1) - e1) / a1;
        const d1 = matrix.d, f1 = matrix.f, y1 = event.y, y2 = y1; //ÊîæÂ§ßÂà∞‰∏≠ÂøÉÁÇπ y2 = this._canvas.height / 2
        const f = (y2 - scale * (y1 - f1) - f1) / d1;
        this._ctx.transform(scale, 0, 0, scale, e, f);
        this.redraw();
    }
    destroy() {
        this._canvas.removeEventListener("dblclick", this._onDoubleClick);
        this._canvas.removeEventListener("mousedown", this._onMouseDown);
        this._canvas.removeEventListener("mousemove", this._onMouseMove);
        this._canvas.removeEventListener("mouseup", this._onMouseUp);
        this._canvas.removeEventListener("wheel", this._onWheel);
    }
}
